import { createStore } from 'vuex'
import axios from 'axios'

const store = createStore({
    state(){
        return{
            name : 'kim',
            age : 20,
            likes : 30,
            liked : false,
            more : {},
        }
    },
    mutations : {
        setMore(state, data){
            state.more = data
        },
        이름변경(state){
            state.name = 'Park'
        },
        나이증가(state){
            state.age += 1;
        },
        좋아요증가(state){
            if(state.liked == false){
                state.likes+=1;
                state.liked = true;
            } else if(state.liked == true){
                state.likes--;
                state.liked = false;
            }
        },
    },
    actions : {
        getDate(context){
            axios.get('https://codingapple1.github.io/vue/more0.json').then((a)=>{
                console.log(a.data);
                context.commit('setMore', a.data)
            })
        },
    }
    
})

export default store;