<template>
  <div>
    <div v-if="step == 0">
      <Post v-for="(a, i) in posting" :key="i" :posting="posting[i]"/>
    </div>
    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div :class="`upload-image ${filter}`" :style="{backgroundImage : `url(${img})`}"></div>
      <div class="filters">
        <FilterBox @click="$emit('filter', a)" :filtersName="a" v-for="(a,i) in filters" :key="i" :img="img">
          <template v-slot:a>{{a}}</template>
        </FilterBox>
      </div>
    </div>
    

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div :class="`upload-image ${filter}`" :style="{backgroundImage : `url('${img}')`}"></div>
      <div class="write">
        <textarea class="write-box" @change="$emit('write', $event.target.value)"></textarea>
      </div>
    </div>
  </div>

  <div v-if="step == 3">
    <Mypage :one="1"/>
  </div>

  <button>게시물</button>
  <button>필터</button>
  <button>글쓰기</button>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";
import Mypage from './Mypage.vue';

export default {
  name: "Container",
  data(){
    return{
      filters : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
"inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
"reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
    };
  },
  components: {
    Post: Post,
    FilterBox: FilterBox,
    Mypage : Mypage,
  },
  props: {
    posting: Array,
    step : Number,
    img : String,
    filter : String,
  },
};
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>