<template>
  <div style="padding: 10px">
    <div style="padding: 10px">
      <h4>팔로워</h4>
      <input @input="doTish" v-model="search" placeholder="?" />{{search}}
      <div class="post-header" v-for="(a, i) in follower" :key="i">
        <div class="profile" :style="{backgroundImage : `url(${follower[i].image})`}"></div>
        <span class="profile-name">{{follower[i].name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import {ref, onMounted, reactive} from 'vue';
import axios from 'axios';
export default {
    name : 'Mypage',
    props : {
        one : Number,
    },
    setup(){
        let follower = ref([]); // 관습적으로 레퍼런스 데이터 타입 제외한 나머지
        let follower2 = reactive([]);
        let search = ref('');

        onMounted(()=>{
            axios.get('./follower.json').then((a)=>{
                follower.value = a.data;
                follower2.value = [...a.data];
            });
        })

        function doTish(){
          // 만약에 검색창에 follower라는 데이터의 이름이 들어가면
          // 들어간 이름을 찍게
          let result = follower2.value.filter((a)=>{
            return a.name.includes(search.value) == true
          })
          console.log(result);
          console.log(follower.value)
          console.log(follower2.value)

          follower.value = [...result]

          // if(result.length > 0){
          //   follower.value = [...result];
          // }
          // if(search.value == ""){
          //   follower.value = follower2.value;
          //   console.log('빈값')
          // }
        }

        return {follower, search, doTish, follower2};
    },
    data(){
        return {

        }
    },
};
</script>

