<script>
import Container from "./components/Container";
import posting from "./assets/posting.js";
//import axios from 'axios';
import {mapState, mapMutations} from "vuex";

export default {
  name: "App",
  data() {
    return {
      posting: posting,
      moreBtn: 1,
      step: 3,
      img: "",
      textest : '',
      filter : '',
      testFilter : '',
      카운터 : 0,
    };
  },
  mounted(){
    this.emitter.on('작명', (a)=>{
      this.testFilter = a;
      console.log(this.testFilter);
    });
    this.emitter.on('좋아요', (a)=>{
      if(a.liked === false){
        a.likes += 1;
        a.liked = true;
      } else if(a.liked === true){
        a.likes -= 1;
        a.liked = false;
      }
    })
  },
  components: {
    Container: Container,
  },
  computed : {
    ...mapState(['name', 'age', 'likes']),
    ...mapState({작명 : 'name'})
  },
  methods: {
    ...mapMutations(['나이증가']),
    now(){
      return new Date();
    },
    // more(){
    //   if(this.moreBtn == 1){
    //     axios.get('https://codingapple1.github.io/vue/more0.json').then((data)=>{
    //       this.posting.push(data.data);
    //       console.log(this.posting);
    //     });
    //     this.moreBtn += 1;
