    //       this.moreBtn += 1;
    //     });
    //   }
    // },
    upload(e) {
      let 파일 = e.target.files;
      let url = URL.createObjectURL(파일[0]);
      this.img = url;
      this.step = 1;
      e.target.value = '';
      console.log(url);
      console.log(파일);
    },
    prev() {
      if (this.step == 0) {
        this.step = 0;
      } else {
        this.step--;
      }
    },
    publish() {
      let 이미지 = {
        name: "Kim Hyun",
        userImage: this.img,
        postImage: this.img,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.textest,
        filter: this.filter,
      };
      this.posting.unshift(이미지);
      this.step = 0;
    },
  },
};
</script>

